import React, { Fragment } from 'react'
import { Route, Switch, Link } from 'react-router-dom'
import routes from '../../routes'
import {NavbarTop} from '../navbar/navbar'
const App = () => {
  return (
    <Fragment>
<header>
        <NavbarTop />
</header>
      <Switch>
        {routes.map(route => (
          <Route
            key={route.path}
            path={route.path}
            exact={route.exact}
            component={route.component}
          />
        ))}
      </Switch>
      <footer>Footer</footer>
    </Fragment>
  )
}

